  text-shadow: 0 0 8px rgba(0, 0, 0, 0.8);
}

.banner__profile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: center center url("../images/banner-bg.png");
  background-size: cover;
}

.banner__profile__name {
  margin-bottom: 8px;
  font-size: 2.4rem;
  line-height: 1;
}

.banner__profile__description {
  margin-bottom: 16px;
  font-size: 1.2rem;
}

.banner__profile__social {
  display: flex;
}

.banner__profile__social__button {
  display: block;
  width: 40px;
  height: 40px;
  margin: 2px;
  padding-top: 4px;
  background: rgba(0, 0, 0, 0.5);
  color: #eee;
  font-size: 1.2rem;
  line-height: 36px;
  text-decoration: none;
}

.banner__profile__social__button:hover {
  background: rgba(0, 0, 0, 0.9);
  color: #dca;
}

.section--grey {
  background: #f1f2f3;
}

.section__header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 96px;
  margin-bottom: 16px;
  background: #111;
  color: #fff;
}

.section__header__title {
  font-size: 1.6rem;
}

.section__header__title-ruby {
  font-family: "open-sans", sans-serif;
  letter-spacing: .16rem;
  text-transform: uppercase;
  opacity: .6;
}

.career-history {
  max-width: 1024px;
  margin: 0 auto;
  padding: 32px 16px 64px;
}

.career-history__item {
  display: flex;
  align-items: center;
}

.career-history__item__vert-line {
  align-self: stretch;
  width: 4px;
  margin: 0 10px;
  background: #ddd;
}

@media screen and (max-width: 619px) {
  .career-history__item__vert-line {
    display: none;
  }
}

.career-history__item:first-of-type .career-history__item__vert-line {
  transform: scaleY(0.5);
  transform-origin: 0 100%;
}

.career-history__item:last-of-type .career-history__item__vert-line {
  transform: scaleY(0.5);
  transform-origin: 0 0;
}

.career-history__item__pin {
  width: 24px;
  height: 24px;
  border: 4px solid #fff;
  border-radius: 100%;
  margin-left: -24px;
  background: #dca;
  transform: translate(0, 0);
}

@media screen and (max-width: 619px) {
  .career-history__item__pin {
    display: none;
  }
}

.career-history__item__body-bubble {
  border: 12px solid transparent;
  border-left: none;
  border-right-color: #fff;
  margin-left: 16px;
}

@media screen and (max-width: 619px) {
  .career-history__item__body-bubble {
    display: none;
  }
}

.career-history__item__body {
  margin: 8px 0;
  padding: 12px 20px;
  flex: 1;
  background: #fff;
}

@media screen and (max-width: 619px) {
  .career-history__item__body {
    border-left: 4px solid #ddccaa;
  }
}

.career-history__item__date {
  opacity: .6;
}

.career-history__item__title {
  font-size: 1.2rem;
}

.skill {
  display: flex;
  flex-wrap: wrap;
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 16px 64px 16px;
}

.skill__category {
  width: 100%;
  margin: 32px 0;
  text-align: center;
}

.skill__category__label {
  color: #269;
  font-size: 1.6rem;
  line-height: 1;
}

.skill__category__label-ruby {
  font-size: .8rem;
  font-family: "open-sans", sans-serif;
  letter-spacing: .12rem;
  text-transform: uppercase;
  opacity: .6;
}

.skill__item {
  width: 30%;
  margin: 0 1.66% 24px 1.66%;
}

@media screen and (max-width: 619px) {
  .skill__item {
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
  }
}

.skill__item__info {
  display: flex;
  padding-bottom: 4px;
}

.skill__item__name {
  overflow: auto;
  white-space: nowrap;
  text-overflow: ellipsis;
  flex: 1;
}

.skill__item__progress {
  text-align: right;
  opacity: .6;
}

.skill__item__progress-bar__bg {
  height: 6px;
  background: #eee;
}

.skill__item__progress-bar__fg {
  height: 100%;
  background: #dca;
}

.skill__item__progress-bar__fg[data-aos='progress'] {
  transform: scaleX(0);
  transform-origin: 0 0;
}

.skill__item__progress-bar__fg[data-aos='progress'].aos-animate {
  transform: scaleX(1);
}

.footer {
  padding: 16px 32px;
  background: #f5f6f7;
  color: #444;
  text-align: center;
}

.footer__license {
  color: #777;
}

.footer__license a {
  color: #444;
}

.footer__license a:hover {
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

*::selection {
  background: #ddccaa;
  color: #fff;
}

html {
  font-family: "kozuka-gothic-pro", sans-serif;
  line-height: 1.6;
}

body {
  background: #fff;
  color: #333;
}

.seo {
  display: none;
}
